
import { mapGetters } from "vuex";
import store from "@/store/index"
export default {
    // data for getting logged in data from the store. By this data show the login/logout links!
    computed: {
        ...mapGetters({
            user: "getUser",
        }),
    },
    methods: {
        logOut(): void {
            store.dispatch("logOut")
        }
    }
};
